.main-content {
    overflow-x: hidden;
}
.container-rules {
    display: -ms-grid;
    display: grid;
    grid-template-columns: repeat(3, minmax(350px,1fr));
    grid-auto-rows: auto;
    gap: 3px;
    min-height: 600px;
    padding: 10px;
}


